import {
  ArrowForwardIos,
  Close,
  Person,
  PhoneIphone,
} from "@mui/icons-material";
import { IconButton } from "@mui/material";
import { useState } from "react";

import AskListItem from "../ask/AskListItem";

import styles from "../../styles/mypage/Frames.module.css";
import { NavLink } from "react-router-dom";

function Frames({ certs, asks, shares, user, attrCerts }) {
  function deleteHandler() {
    // DB에서 날리기
  }

  const [isVisible, setIsVisible] = useState(true);

  const handleClick = (e) => {
    setIsVisible((current) => !current);
  };

  return (
    <div>
      <div className={styles.mypageInnerContainer}>
        <div className={styles.frameTitle}>
          <h2>나의 텃밭</h2>
          <IconButton aria-label="details" href="/mypage/userinfo">
            <ArrowForwardIos className={styles.details} />
          </IconButton>
        </div>
        <div className={styles.frameContent}>
          <div className={styles.userInfo}>
            <h5>회원정보</h5>
            <div className={styles.userContent}>
              <Person color="action" inheritViewBox />
              <p className={styles.userDetail}>{user.userName}</p>
            </div>
            <div className={styles.userContent}>
              <PhoneIphone color="action" inheritViewBox />
              <p className={styles.userDetail}>{user.userTel}</p>
            </div>
          </div>
          <div className={styles.earned}>
            <h5>취득한 자격증</h5>

            {certs.map((cert) => (
              <div className={styles.earnedContent} key={cert.id}>
                <p className={styles.earnedCertName}>{cert.certName}</p>
                <p className={styles.earnedCertDate}>
                  취득일: {cert.earnedDate}
                </p>
              </div>
            ))}
          </div>
        </div>
      </div>

      <div className={styles.mypageInnerContainer}>
        <div className={styles.frameTitle}>
          <h2>내가 쓴 글</h2>
          <IconButton aria-label="details" href="/mypage/written">
            <ArrowForwardIos className={styles.details} />
          </IconButton>
        </div>

        <div className={styles.frameContent}>
          <div>
            <div className={styles.writtenTab}>
              <li>
                <input
                  className={styles.radioBtn}
                  type="radio"
                  name="tab"
                  id="ask"
                  defaultChecked
                  hidden
                  onClick={handleClick}
                />
                <label className={styles.tabs} htmlFor="ask">
                  무물방
                </label>
              </li>
              <li>
                <input
                  className={styles.radioBtn}
                  type="radio"
                  name="tab"
                  id="share"
                  hidden
                  onClick={handleClick}
                />
                <label className={styles.tabs} htmlFor="share">
                  나눔
                </label>
              </li>
            </div>
          </div>

          {isVisible && (
            <div className={styles.askList}>
              {asks.map(
                (ask) =>
                  ask.id === 1 && (
                    <NavLink key={ask.id} to={`/community/ask/${ask.id}`}>
                      <AskListItem key={ask.id} ask={ask} />
                    </NavLink>
                  )
              )}
            </div>
          )}

          {!isVisible && (
            <div>
              {shares.map(
                (share) =>
                  share.id === 1 && (
                    <NavLink key={share.id} to={`/community/share/${share.id}`}>
                      <div className={styles.shareList} key={share.id}>
                        <img
                          src={share.itemImg}
                          alt="나눔이미지 파일"
                          className={styles.shareFile}
                        />
                        <div className={styles.shareContainer}>
                          <div className={styles.shareListHeader}>
                            <h1 className={styles.writtenTitle}>
                              {share.shareTitle}
                            </h1>
                            <p className={styles.shareRegDate}>
                              {share.regDate}
                            </p>
                          </div>
                          <p className={styles.shareContent}>{share.content}</p>
                        </div>
                      </div>
                    </NavLink>
                  )
              )}
            </div>
          )}
        </div>
      </div>

      <div className={styles.mypageInnerContainer}>
        <div className={styles.frameTitle}>
          <h2>관심 자격증</h2>
        </div>
        <div className={styles.frameContent}>
          <div className={styles.attactive}>
            {attrCerts.map((attrCert) => (
              <div key={attrCert.id} className={styles.attractiveCert}>
                <a href={`/cert/${attrCert.certId}`}>{attrCert.certName}</a>
                <IconButton
                  aria-label="delete"
                  className="deleteBtn"
                  onClick={deleteHandler}
                >
                  <Close fontSize="small" className={styles.deleteBtn} />
                </IconButton>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Frames;
